.st-button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-style: none;
  box-sizing: border-box;
  width: 161.45px;
  height: 44px;
  border-radius: 22px;
  cursor: pointer;
  background: #ffffff;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  color: #d41342;
  & svg {
    margin-left: 8px;
    stroke: #d41342;
  }
  &:hover,
  &:focus,
  &:active {
    background: #d41342;
    color: #fff;
    & svg,
    & svg,
    & svg {
      stroke: #fff;
      fill: #fff;
    }
  }
  z-index: 2;
}

.st-small-button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-style: none;
  box-sizing: border-box;
  padding: 7px 15px;
  border-radius: 20px;
  cursor: pointer;
  background: #ffffff;
  font-size: 8px;
  line-height: 1.3;
  color: #d41342;
  & svg {
    stroke: #d41342;
  }
  &:hover,
  &:focus,
  &:active {
    background: #d41342;
    color: #fff;
    & svg,
    & svg,
    & svg {
      stroke: #fff;
    }
  }
}
@media screen and (min-width: 1200px) {
  .st-small-button {
    font-size: 14px;
  }
}
